import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import axios from 'axios';
import { Nav } from 'react-bootstrap';
import ProfileEditForm from "../user/ProfileEditForm.js";
import { decode } from "jsonwebtoken";

export default class Profile extends Component {

  constructor(props) {
    super(props)
    this.state = {
       user:this.props.user,
       userData:[],
       ModalShow: false
    }

  }


  
  profileEditHandler = (user) => {
    axios 
        .put("tripella/user/update", user)
        .then((response) => {
          console.log(user);
          console.log(response);
          this.setState({
            userData:response.data
          })
        })
        .catch((error) => {
          console.log(error);
        });
  };

  profileHandler = () => {
    axios 
        .get("tripella/user/detail")
        .then((response) => {
          console.log(response);
          this.setState({
            userData:response.data
          })

        })
        .catch((error) => {
          console.log(error);
        });
  };

  componentDidMount() {
    this.profileHandler()
}

  render() {

    let ModalClose =() => this.setState({ModalShow: false});
    return (
      
      <div className="3w-container w3-content" >
      <div className=" w3-container w3-round-xxlarge w3-white w3-card-4  w3-margin w3-row-padding" style={{width:"900px"}}>
          <div class="w3-container w3-center">
            <img src="data:image/jpeg;base64," alt="Avatar" style={{ width: "20%", marginTop: "20px" }} class="w3-circle" />
            <br />
            <br />
            
        <h2>{this.state.userData.firstName} {this.state.userData.lastName} {this.state.userData.id}</h2>
            <h2>{this.state.user ? this.state.user.sub : null}</h2> 
            
            <br />
            <p>Bio</p>
            <br />
            <Router>
            <Link to='/profileeditform'>
   <button color='primary' onClick={() => this.setState({ModalShow: true})}>Edit Profile</button>

</Link>
            <Route
            path="/profileeditform"
            component={() => <ProfileEditForm user={this.state.userData} editUser={this.profileEditHandler} show={this.state.ModalShow}
        onHide={ModalClose}/>}
          ></Route>
            </Router>
          </div>

        </div>
      </div>

      


    )
  }
}
